import React from 'react'
import { Link,Switch,Route } from 'react-router-dom'
import MyProjects from './MyProjects'
import Progress from '../Progress'
import Zoom from 'react-reveal/Zoom';

const AboutMe = () => {
  return (
    <div className='container  '>
      <Zoom top>

       <div className=' flex justify-around items-center w-full  relative top-20 '>
          <div className='   p-4 text-white flex justify-center items-center flex-col '>
             <h1 className=' text-3xl font-bold text-white '>Hi, I am FrontEnd developer</h1>
             <p className=' text-base font-medium mt-2'>Web designer and developer from tamilnadu,india. <br />I create website applications.</p>

             <p className='mt-2'><Link className=' uppercase font-semibold border-2 border-yellow-400 p-1 rounded-lg hover:bg-orange-700 transition duration-200 ease-in hover:text-white ' to='/MyProjects'>My projects</Link></p>
          

          <div className='text-center text-white mt-8'>
             <h3 className='text-3xl font-bold bg-gradient-to-tr from-orange-500 to-green-500 bg-clip-text text-transparent'>My Skills</h3>
             <div className=' flex flex-wrap justify-center mt-4'>
             <h5>HTML   <Progress done="90" /></h5>
             <h5>CSS    <Progress done="85" /></h5>
             <h5>JS     <Progress done="85" /></h5>
             <h5>PHP    <Progress done="75" /></h5>
             <h5>MySQL  <Progress done="70" /></h5>
             <h5>React.Js<Progress done="85" /></h5>
             <h5>Bootstrap<Progress done="80" /></h5>
             <h5>Tailwind-CSS<Progress done="85" /></h5>
             <h5>Material-UI<Progress done="80" /></h5>
             <h5>Chackra-UI<Progress done="70" /></h5>
             <h5></h5>
             </div>
          </div>
          </div>
       </div>
      <Switch>
        <Route path='/MyProjects' component={MyProjects} />
      </Switch>

      </Zoom>
    </div>
  )
}

export default AboutMe
