import React, { useRef,useState } from 'react'
import { useForm } from "react-hook-form"
import '../pages/contact.css'
import Zoom from 'react-reveal/Zoom';
import image2 from '../../images/phone.gif'
import image3 from '../../images/gmail.png'
import image4 from '../../images/location.png'
import image5 from '../../images/instagram.png'
import image6 from '../../images/facebook.png'
import image7 from '../../images/twitter.png'
import emailjs from 'emailjs-com'



const ContactMe = () => {
   
  const formRef = useRef();
  const reset = useForm();
  const [done,setDone] = useState(false);
      
 const handleSubmit = (e) =>{
    e.preventDefault();

    emailjs.sendForm('service_1x96ikd', 'template_slllcdl', formRef.current,'updS99kDUwsSHGJVN')
    .then((result) => {
        console.log(result.text);
        setDone(true)
        return(
          done && " your message was sended..."
        )
    }, (error) => {
        console.log(error.text);
    });


  } 
  
    return (
      <div className='container'>
       <Zoom bottom>   
        <div className='box flex justify-center items-center h-[80vh] '>
         <div className=' bg-gray-900 bg-opacity-40 rounded-2xl  shadow-lg shadow-current  w-4/5 h-3/5 mb-36 hover:scale-110 transition duration-500 ease-in-out mt-6'>
           
         <div className='Res flex justify-center items-center  '>
               <div className='social  flex w-2/4 h-3/5 justify-center items-center flex-col '>
                  <h3 className=' w-56 uppercase font-medium text-yellow-500 mb-8 '>if you hire me please contact me below my social media</h3>  
                    <div className='flex p-1 mr-32 text-white'>
                   <img className=' w-10' src={image2} alt="sample" />+91 8680998312
                   </div>
                   <div className='flex p-2 mr-8 text-white'>
                   <img className=' w-8' src={image3} alt="sample" />mpasupathirock98@gmail.com
                   </div>
                   <div className='flex p-2 text-white'>
                   <img className=' w-8' src={image4} alt="sample" />2-781 salem,tamilnadu,India.636458 
                   </div>
                   <div className=' flex mb-2'>
                     <a href="#"><img className='w-12 hover:scale-110 duration-300 transition ease-in-out ' src={image5} alt="sample" /></a>
                     <a href="#"><img className='w-9 rounded-full hover:scale-110 duration-300 transition ease-in-out' src={image6} alt="sample" /></a>
                     <a href="#"><img className='w-9 rounded-full ml-2 hover:scale-110 duration-300 transition ease-in-out' src={image7} alt="sample" /></a>
                   </div>

               </div>
               
               <div className='chat w-2/4 h-3/5 flex justify-center items-center flex-col p-4'>
                 <h3 className=' font-bold font-serif text-2xl text-orange-400 '>Let's chat me</h3>
                 <form ref={formRef} onSubmit={handleSubmit} className=' flex flex-col p-6 mt-2'>
                 <input className='mt-2 h-10 p-2 text-green-500 bg-transparent border-b-2 border-solid border-t-emerald-400' type="text" name='user_name' placeholder='Name'  required/>
                 <input className='mt-2 h-10 p-2 text-green-500 bg-transparent border-b-2 border-solid border-t-emerald-400' type="text" name='user_subject' placeholder='Subject '/>
                 <input className='mt-2 h-10 p-2 text-green-500 bg-transparent border-b-2 border-solid border-t-emerald-400' type="text" name='user_email' placeholder='Email' required/>
                 <textarea className='mt-2 pl-2 text-green-500 bg-transparent border-b-2 border-solid' rows='2' name='message' placeholder='message' required/>
                 <div className='flex'>
                 <button className=' border-2 w-20 mt-2 rounded-xl  font-semibold uppercase text-green-500 hover:scale-110 transition duration-300 ease-in-out hover:bg-orange-400 hover:text-white'>Send</button>
                 <button className=' border-2 w-20 mt-2 ml-2 rounded-xl  font-semibold uppercase text-green-500 hover:scale-110 transition duration-300 ease-in-out hover:bg-orange-400 hover:text-white'
                   type="reset" onClick={() => {
                     reset({
                      user_name:"",
                      user_subject:"",
                      user_email:"",
                      message:""
                      
                     });
                    }}
                   >Reset
                  </button></div>
                  <div className=' text-yellow-400 font-medium'>{done && "your message was sended..."}</div>
                 </form>
               </div> 
              
            </div>


         </div>
        </div>

       </Zoom> 
     </div>  
    )
}

export default ContactMe
