import { useState } from "react";

const Progress = ({done}) => {
	const [style, setStyle] = useState({});
	
	setTimeout(() => {
		const newStyle = {
			opacity: 1,
			width: `${done}%`
		}
		
		setStyle(newStyle);
	}, 200);
	
	return (
		<div className=" bg-slate-100 rounded-3xl relative h-6 w-48 m-3 ">
			<div className="progress-done bg-gradient-to-l from-green-600 border-t-red-600 rounded-3xl text-yellow-300 font-semibold flex item-center justify-center h-full w-0 opacity-0 transition duration-200 ease-in" style={style}>
				{done}%
			</div>
		</div>
	)
}

export default Progress