import React from 'react'
import image from '../images/PortFolio_img.jpeg'
import { Link,Switch,Route, Redirect } from 'react-router-dom'
import Resume from './pages/Resume'
import MyProjects from './pages/MyProjects'
import ContactMe from './pages/ContactMe'
import AboutMe from './pages/AboutMe'




const Navbar = () => {
    return (
        <div className=' container  w-screen'>
          
            <div className=' flex  justify-around items-center sticky top-0  z-10 uppercase  font-semibold text-white bg-opacity-80 bg-slate-500 '>
            <div className=' flex flex-col justify-center items-center p-1 hover:scale-110 transition duration-300 ease-in-out hover:text-teal-500'>
             <img className=' rounded-full object-cover w-16 h-16' src={image} alt="sample" /><h4 className=' text-center text-red-500 font-bold'> Hi I am Pasupathi </h4></div>
                <Link className='hover:scale-110 transition duration-300 ease-in-out p-1 rounded-lg hover:text-orange-500' exact to='/'>Resume</Link>
                <Link className='hover:scale-110 transition duration-300 ease-in-out p-1 rounded-lg  hover:text-red-500' to='/MyProjects'>projects</Link>
                <Link className='hover:scale-110 transition duration-300 ease-in-out p-1 rounded-lg  hover:text-green-500' to='/about'>about</Link>
                <Link className='hover:scale-110 transition duration-300 ease-in-out p-1 rounded-lg  hover:text-blue-500' to='/contact'>contact me</Link>

                <Redirect to='/' />
            </div>
         <Switch>
         <Route exact path='/' component={Resume}></Route>
         <Route path='/MyProjects' component={MyProjects}></Route>
         <Route path='/about' component={AboutMe}></Route>
         <Route path='/contact' component={ContactMe}></Route>

         
         </Switch>   
        </div>
    )
}

export default Navbar
